.social-list .social-icon {
  -webkit-transition: background-color 0.2s ease 0s;
  -o-transition: background-color 0.2s ease 0s;
  transition: background-color 0.2s ease 0s; }
  .social-list .social-icon.social-icon-github {
    border-color: #333; }
    .social-list .social-icon.social-icon-github .mdi-icon {
      fill: #333; }
    .social-list .social-icon.social-icon-github:hover {
      background-color: #333; }
  .social-list .social-icon.social-icon-linkedin {
    border-color: #0077b5; }
    .social-list .social-icon.social-icon-linkedin .mdi-icon {
      fill: #0077b5; }
    .social-list .social-icon.social-icon-linkedin:hover {
      background-color: #0077b5; }
  .social-list .social-icon.social-icon-pdf {
    border-color: #ff0000; }
    .social-list .social-icon.social-icon-pdf .mdi-icon {
      fill: #ff0000; }
    .social-list .social-icon.social-icon-pdf:hover {
      background-color: #ff0000; }
  .social-list .social-icon .mid-icon {
    -webkit-transition: fill 0.2s ease 0s;
    -o-transition: fill 0.2s ease 0s;
    transition: fill 0.2s ease 0s; }
  .social-list .social-icon:hover .mdi-icon {
    fill: white; }



